import React from 'react'
import { Link } from 'react-router-dom'

const Navbar = () => {
  return (
    <div style={{display:"flex", gap:"40px"}}>
        <Link to={"/login"}>Login Page</Link>
        <Link to={"/restaurant"}>Restaurant page</Link>
        <Link to={"/cart"}>Cart</Link>
    </div>
  )
}

export default Navbar