import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getData } from "../redux/AppReducer/action";
import { Box, Grid, GridItem } from "@chakra-ui/react";
import Card from "../components/Card";

const Restaurant = () => {
  const RestaurantRedux = useSelector((state) => state.AppReducer);
  const reduxLogin = useSelector((state) => state.AuthReducer)
  console.log(reduxLogin,"llll")
  const data = RestaurantRedux.restaurant.data;
  const dispatch = useDispatch();
  const [page, setPage] = useState(1);


  //for sorting by price
  const [sortData,setSortData] = useState(data)
  const [sortPriceValue,setsortPriceValue] = useState("")

  const [sortRateValue,setsortRateValue] = useState("")

  const byPrice = () => {
    // console.log(sortPriceValue)
    if(sortPriceValue == "ASC")
    {
        let sortdata = data.sort((a, b) => {
            return a.price_starts_from - b.price_starts_from;
          });
      
          setSortData([...sortdata]);
    }
    if(sortPriceValue == "DSC")
    {
        let sortdata = data.sort((a, b) => {
            return b.price_starts_from - a.price_starts_from;
          });
      
          setSortData([...sortdata]);
    }
  }

  const byRate = () => {

    if(sortRateValue == "ASC")
    {
        let sortdata = data.sort((a, b) => {
            return a.rating - b.rating;
          });
      
          setSortData([...sortdata]);
    }
    if(sortRateValue == "DSC")
    {
        let sortdata = data.sort((a, b) => {
            return b.rating - a.rating;
          });
      
          setSortData([...sortdata]);
    }
    
  }

  useEffect(() => {
    // const param = {
    //     page : 1,
    //     limit : 10
    // }
    dispatch(getData(page));
    // console.log(RestaurantRedux,"use")
  }, [page]);
  // console.log(RestaurantRedux,"HI")
  // console.log(data,"BYE")
  return (
    <>
      <Box>
        <Box>
        <label>Sort by Price :</label>
        <select onChange={(e) => {setsortPriceValue(e.target.value)}} onClick={byPrice}>
            <option value="">None</option>
            <option value="ASC">ascending</option>
            <option value="DSC">descending</option>
        </select>
        </Box>
        <Box>
        <label>Sort by Rating : </label>
        <select onChange={(e) => {setsortRateValue(e.target.value)}} onClick={byRate} >
            <option value="">None</option>
            <option value="ASC">ascending</option>
            <option value="DSC">descending</option>
        </select>
        </Box>
        <Grid templateColumns="repeat(4, 1fr)" gap={6}>
          {data?.map((item) => (
            <GridItem>
              <Card {...item} />
            </GridItem>
          ))}
        </Grid>
        <br /><br />
        <div style={{display:"flex",justifyContent:"space-around", border:"1px dotted black"}}>
            <div onClick={() => setPage(1)} >1</div>
            <div onClick={() => setPage(2)}>2</div>
            <div onClick={() => setPage(3)}>3</div>
            <div onClick={() => setPage(4)}>4</div>
            <div onClick={() => setPage(5)}>5</div>
        </div>
      </Box>
    </>
  );
};

export default Restaurant;
