import {
  Table,
  Thead,
  Tbody,
  Tfoot,
  Tr,
  Th,
  Td,
  TableCaption,
  TableContainer,
  Button,
  HStack,
} from "@chakra-ui/react";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  PinInput,
  PinInputField,
} from "@chakra-ui/react";
import { useDisclosure } from "@chakra-ui/react";

import React from "react";
import { useState } from "react";
import Counter from "../components/Counter";

const Cart = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [one,setOne] = useState("")
  const [two,setTwo] = useState("")
  const [three,setThree] = useState("")
  const [four,setFour] = useState("")

  const data = JSON.parse(localStorage.getItem("contest9"));
  console.log(data);

  const handelOTP = () => {
    if(one == 1 && two == 2 && three == 3 && four == 4)
    {
        alert("“Order placed successfully“")
    }
    else{
        alert("wrong OTP")
    }

  }

  return (
    <>
      <TableContainer>
        <Table variant="simple">
          {/* <TableCaption>Imperial to metric conversion factors</TableCaption> */}
          <Thead>
            <Tr>
              <Th>NAME</Th>
              <Th>PRICE</Th>
              <Th>TYPE</Th>
              <Th>Qty</Th>
              <Th>DELETE</Th>
            </Tr>
          </Thead>

          <Tbody>
            {data?.map((item) => (
              <Tr>
                <Td>{item.name}</Td>
                <Td>{item.price_starts_from}</Td>
                <Td>{item.type}</Td>
                <Td>{<Counter />}</Td>
                <Td>
                  <Button>DELETE</Button>
                </Td>
              </Tr>
            ))}
            {/* <Td>inches</Td>
        <Td>millimetres (mm)</Td>
        <Td isNumeric>25.4</Td> */}
          </Tbody>
          {/* <Tfoot>
      <Tr>
        <Th>To convert</Th>
        <Th>into</Th>
        <Th isNumeric>multiply by</Th>
      </Tr>
    </Tfoot> */}
        </Table>
        <Button onClick={onOpen}>SUBMIT</Button>
      </TableContainer>
      <Modal isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>Modal Title</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <HStack>
              <PinInput>
                <PinInputField onChange={(e) => {setOne(e.target.value)}} />
                <PinInputField onChange={(e) => {setTwo(e.target.value)}} />
                <PinInputField onChange={(e) => {setThree(e.target.value)}} />
                <PinInputField onChange={(e) => {setFour(e.target.value)}} />
              </PinInput>
            </HStack>
          </ModalBody>

          <ModalFooter>
            <Button mr={3} variant="ghost" onClick={onClose}>
              Close
            </Button>
            <Button colorScheme="blue" onClick={handelOTP}>SUBMIT OTP</Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
};

export default Cart;
