import * as types from './actionType';
import axios from 'axios'


const getReq = () => {
    return {
        type : types.GET_RESTAURANT_REQUEST
    }
}

const getSuccess = (payload) => {
    return {
        type : types.GET_RESTAURANT_SUCCESS,
        payload
    }
}

const getFailure = () => {
    return {
        type : types.GET_RESTAURANT_FAILURE
    }
}

const getData = (page) => {
    return(dispatch) => {
        dispatch(getReq());
        axios.get(`https://dbioz2ek0e.execute-api.ap-south-1.amazonaws.com/mockapi/getrestaurants?page=${page}&limit=20`).then((res) => dispatch(getSuccess(res.data))).catch((e) => dispatch(getFailure()))

    }
}
