import React from "react";
import { Box, Button, Image, Text } from "@chakra-ui/react";

const Card = (props) => {
//   console.log(props, "props");


const handleCart = () => {
    let localData = JSON.parse(localStorage.getItem("contest9")) || []
    
    localData.push(props);
    localStorage.setItem("contest9",JSON.stringify(localData))
    // console.log(props)
}

  return (
    <Box key={props.id} borderWidth='1px' borderRadius='lg'>
      <Box>
        <Image src={props.image} />
      </Box>

      <Box>
        <Text>Name : {props.name}</Text>
      </Box>
      <Box>
        <Text>Votes : {props.number_of_votes}</Text>
      </Box>

      <Box>
        <Text>Price starts : {props.price_starts_from}</Text>
      </Box>

      <Box>
        <Text>Rating : {props.rating}</Text>
      </Box>

      <Box>
        <Text>Type : {props.type}</Text>
      </Box>
      <Button onClick={handleCart}>ADD TO CART</Button>
    </Box>
  );
};

export default Card;
