import * as types from './actionTypes';
import axios from 'axios';

const loginReq = () => {
    return {
        type : types.LOGIN_REQUEST
    }
}

const loginSuccess = (payload) => {
    return {
        type : types.LOGIN_SUCCESS,
        payload
    }
}

const loginFailure = () => {
    return {
        type : types.LOGIN_FAILURE
    }
}



const login = (payload) => {
    return(dispatch) => {
        dispatch(loginReq());
        axios.post("https://reqres.in/api/login",payload).then((res) => dispatch(loginSuccess(res.data))).catch((err) => dispatch(loginFailure()))
    }
}
export {login}